<template>
    <layout-page>
        <div class="card-header">
            <div class="row">
                <div class="col-12 ps-4 pt-3 ">
                    <div class="float-start">
                        <h5>Adicionar Natureza Financeira</h5>
                    </div>
                    <div class="float-end">
                        <button-widget cor="azul" href="./index" tamanho="M">
                            Voltar
                        </button-widget>
                    </div>
                </div>

            </div>
        </div>
        <div class="card-body">
            <div class="row">
                <FormNaturezaFinanceiras></FormNaturezaFinanceiras>
                    <div class="col-4">
                    <button class="btn btn-primary mt-4" type="button" @click="sendForm">Salvar</button>
                    </div>
                </div>
        </div>
    </layout-page>
</template>
<script>
import ButtonWidget from "@/components/widget/buttonWidget.vue";
import LayoutPage from "@/components/page/layoutPage.vue";
import FormNaturezaFinanceiras from "@/views/natureza_financeiras/FormNaturezaFinanceiras.vue";
import natureza_financeiraService from "@/services/natureza_financeira.service";
import toastr from "toastr/build/toastr.min";

export default {
    name: "CreateNaturezaFinanceiras",
    components: {FormNaturezaFinanceiras, LayoutPage, ButtonWidget},
    methods:{
        async sendForm(){
            let dataForm = {
        nome: document.getElementById('nome').value,
descritivo: document.getElementById('descritivo').value,


        }
            let natureza_financeirasService = new natureza_financeiraService();
            let response = await natureza_financeirasService.store(dataForm);

            if(response.data?.id){
                location.href = '/natureza_financeiras/index';
            }else{
                if (response.response.data?.message){
                    toastr.error(response.response.data?.message);
                }else{
                    toastr.error('Houve um problema ao inserir');
                }

            }
        }
    }
}
</script>
<style scoped>
</style>
